<template>
    <div class="main-header">
      <h1>我们在一起</h1>
      <!--有一个需要注意的点就是js里面的月份从0-11开始，所以对应的时间需要自己更改计算-->
      <h2>{{parseInt((new Date().getTime() - new Date(2018, 9, 10).getTime()) / 24 / 60 / 60 / 1000)}}天</h2>
      <h2>{{message}}</h2>
      <h1>距离100天还有{{left}}天</h1>
      <h2>距离零点还有{{date}}</h2>
      <!--<input type="text" v-model="message">-->
      <!--<button @click="counter++">click here</button>-->
      <!--<p >{{counter}}</p>-->
    </div>
</template>

<script>
export default {
  data () {
    return {
      message: '啦',
      counter: '',
      left: 100 - parseInt((new Date().getTime() - new Date(2018, 9, 10).getTime()) / 24 / 60 / 60 / 1000),
      date: (24 - new Date().getHours()) + '小时' + (60 - new Date().getMinutes()) + '分' + (60 - new Date().getSeconds())+ '秒'
    }
  },
  mounted () {
    let _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      _this.date = (24 - new Date().getHours()) + '小时' + (60 - new Date().getMinutes()) + '分' + (60 - new Date().getSeconds()) + '秒' // 修改数据date
    }, 1000)
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer)// 在Vue实例销毁前，清除我们的定时器
    }
  }
}
</script>

<style scoped>
.main-header{
  /*border: 1px solid black;*/
  text-align: center;
}
</style>



